
const SelectFieldOptions = ({ inputsNumber, handleInputsSpawnMinus }: any) => {
  let items: any = [];

    for (let i = 0; i < inputsNumber; i++) {
      items.push(
        <div key={i}>
          <input type="text" />
          <button onClick={handleInputsSpawnMinus} id={i.toString()}>
            X
          </button>
        </div>
      );
    }

  return <div>{items}</div>;
};
export default SelectFieldOptions;
